<template>
  <div class="answer-container">
    <ex-header></ex-header>
    <el-card class="el-container">
      <el-col :md="16" class="left">
        <div class="header">
          <h3>{{ title }}</h3>
        </div>
        <div class="content">
          <div class="question-top">
            <div class="question-number">{{ currentIndex + 1 }}</div>
            <div class="question-type">[{{ questionType }}]</div>
            <div class="question-title">{{ currentQuestion.title }}</div>
          </div>
          <div class="question-select">
            <template v-if="currentQuestion.type === 'radio'">
              <div
                v-for="(item, index) in currentQuestion.options"
                :key="item.id"
                class="select-item"
              >
                <span>{{ String.fromCharCode(65 + index) }}:</span>
                <el-radio :label="item" v-model="currentAnswer"></el-radio>
              </div>
            </template>
            <template v-if="currentQuestion.type === 'multi'">
              <div
                v-for="(item, index) in currentQuestion.options"
                :key="item.id"
                class="select-item"
              >
                <span>{{ String.fromCharCode(65 + index) }}:</span>
                <el-checkbox
                  :label="item"
                  v-model="currentAnswer"
                ></el-checkbox>
              </div>
            </template>
          </div>
          <div class="question-bottom">
            <el-button-group>
              <el-button
                type="primary"
                round
                :disabled="!hasPrev"
                @click="toPrev()"
              >
                <i class="el-icon-arrow-left"></i> 上一题
              </el-button>
              <el-button
                type="primary"
                round
                :disabled="!hasNext"
                @click="toNext()"
              >
                下一题 <i class="el-icon-arrow-right el-icon--right"></i>
              </el-button>
            </el-button-group>
          </div>
        </div>
      </el-col>
      <el-col :md="8" class="right">
        <h3>倒计时</h3>
        <div class="countdown">
          <div class="hour-left">{{ countdown.hour[0] }}</div>
          <div class="hour-right">{{ countdown.hour[1] }}</div>
          <div class="split">:</div>
          <div class="minute-left">{{ countdown.minute[0] }}</div>
          <div class="minute-right">{{ countdown.minute[1] }}</div>
          <div class="split">:</div>
          <div class="second-left">{{ countdown.second[0] }}</div>
          <div class="second-right">{{ countdown.second[1] }}</div>
        </div>
        <h3>题目列表</h3>
        <div class="question-button">
          <el-button
            plain
            v-for="(item, index) in questions"
            :key="item.id"
            :type="buttonType(item, index)"
            @click="buttonClick(item, index)"
          >
            {{ index + 1 }}
          </el-button>
        </div>
        <el-button
          type="success"
          round
          class="submit-exam"
          :loading="submiting"
          @click="handleSubmit()"
          >提交</el-button
        >
      </el-col>
    </el-card>
  </div>
</template>

<script>
import Header from "../../components/front/Header";
import { mapActions } from "vuex";
export default {
  name: "answer",
  components: {
    "ex-header": Header
  },
  data() {
    return {
      timer: null,
      currentTime: Date.now() / 1000,
      title: "",
      notes: "",
      startTime: 0,
      endTime: 0,
      answerTime: 1,
      remainingTime: 1,
      questions: [],
      currentIndex: 0,
      answers: [],
      submiting: false
    };
  },
  computed: {
    countdown() {
      let countdown = this.remainingTime;
      let hour = (countdown % (60 * 60 * 24)) / (60 * 60);
      let minute = (countdown % (60 * 60)) / 60;
      let second = countdown % 60;
      return {
        hour: [Math.floor(hour / 10), Math.floor(hour % 10)],
        minute: [Math.floor(minute / 10), Math.floor(minute % 10)],
        second: [Math.floor(second / 10), Math.floor(second % 10)]
      };
    },
    questionType() {
      let types = {
        radio: "单选题",
        multi: "多选题"
      };
      return types[this.currentQuestion.type];
    },
    currentQuestion() {
      if (this.questions.length <= this.currentIndex) {
        return {
          type: "radio",
          id: 0,
          title: "",
          options: [],
          answer: "",
          score: 0
        };
      }
      return this.questions[this.currentIndex];
    },
    currentAnswer: {
      get() {
        if (this.answers[this.currentIndex] !== undefined) {
          return this.answers[this.currentIndex];
        }
        if (this.currentQuestion.type === "multi") {
          return [];
        }
        return null;
      },
      set(value) {
        this.$set(this.answers, this.currentIndex, value);
      }
    },
    hasPrev() {
      return this.currentIndex !== 0;
    },
    hasNext() {
      return this.currentIndex !== this.questions.length - 1;
    }
  },
  methods: {
    ...mapActions("answer", ["getAnswers", "submitAnswer"]),
    buttonType(item, index) {
      if (this.currentIndex === index) {
        return "primary";
      }
      if (
        this.answers[index] &&
        (!(this.answers[index] instanceof Array) ||
          this.answers[index].length > 0)
      ) {
        return "success";
      }
    },
    buttonClick(item, index) {
      this.currentIndex = index;
    },
    toPrev() {
      this.currentIndex--;
    },
    toNext() {
      this.currentIndex++;
    },
    loadAnswers() {
      this.getAnswers(this.$route.params.id)
        .then(res => {
          this.title = res.data.data.title;
          this.notes = res.data.data.notes;
          this.startTime = res.data.data.start_time;
          this.endTime = res.data.data.end_time;
          this.answerTime = res.data.data.answer_time;
          this.remainingTime = res.data.data.time;
          let questions = [];
          for (const key in res.data.data.questions) {
            for (const item of res.data.data.questions[key]) {
              questions.push({
                ...item,
                type: key,
                options: JSON.parse(item.options.replace(/'/g, '"'))
              });
            }
          }
          this.$set(this, "questions", questions);
          this.timer = setInterval(() => {
            this.remainingTime--;
            this.checkAndSubmit();
          }, 1000);
        })
        .catch(err => {
          this.$notify.error({
            title: "获取失败",
            message: err.data.error,
            duration: 1000,
            onClose: () => {
              if (err.status === 401) {
                this.$router.push({ path: "/" });
              }
            }
          });
        });
    },
    handleSubmit() {
      let hasComplete = this.answers.length === this.questions.length;
      if (hasComplete) {
        for (const item of this.answers) {
          if (
            item === undefined ||
            (item instanceof Array && item.length <= 0)
          ) {
            hasComplete = false;
            break;
          }
        }
      }
      if (!hasComplete) {
        this.$confirm("还有未完成的题目, 是否继续提交?", "提交", {
          confirmButtonText: "提交",
          cancelButtonText: "取消",
          type: "warning"
        })
          .then(() => {
            this.submitExam();
          })
          .catch(() => {
            this.$message({
              type: "info",
              message: "已取消提交"
            });
          });
      } else {
        this.submitExam();
      }
    },
    submitExam() {
      this.submiting = true;
      let answers = [];
      for (let i = 0; i < this.answers.length; i++) {
        let answer = this.answers[i];
        let answer_str = "";
        if (!(answer instanceof Array)) {
          answer = [answer];
        }
        for (const option of answer) {
          if (option) {
            answer_str += String.fromCharCode(
              65 + this.questions[i].options.indexOf(option)
            );
          }
        }
        answers.push(answer_str);
      }
      for (let i = this.answers.length; i < this.questions.length; i++) {
        answers.push("");
      }
      this.submitAnswer({
        id: this.$route.params.id,
        answers: answers
      })
        .then(res => {
          this.submiting = false;
          this.$alert("提交成功，得分：" + res.data.data, "提交成功", {
            confirmButtonText: "返回",
            callback: () => {
              this.$router.push({ path: "/rank" });
            }
          });
        })
        .catch(err => {
          this.submiting = false;
          this.$notify.error({
            title: "提交失败",
            message: err.data.error
          });
        });
    },
    checkAndSubmit() {
      if (this.remainingTime > 0 || Date.now() / 1000 < this.endTime) {
        return;
      }
      this.submitExam();
    }
  },
  mounted() {
    this.loadAnswers();
  },
  destroyed() {
    clearInterval(this.timer);
  }
};
</script>

